import React, { PureComponent } from 'react';

import { inject, observer } from 'mobx-react';

import Dialog from 'components/Dialog';
import { GraphStoreType } from '../../../stores/graph/graphStore';

type DifferentSumProps = {
  graphStore?: GraphStoreType;
  [key: string]: any;
};

/**
 * Компонент модального окна по наличию расхождения в суммах НДС между графом и витриной
 */
@inject('graphStore')
@observer
class DifferentSum extends PureComponent<DifferentSumProps> {
  constructor(props: DifferentSumProps) {
    super(props);
  }

  _send = async () => {
    const { graphStore, onClose } = this.props;
    await graphStore?.fetchChangeSchemaMDAB();
    onClose();
  };

  render() {
    return (
      <Dialog
        withOverlay={false}
        options={{
          opened: true,
          type: 'confirm',
          title: 'Предупреждение',
          okText: 'Сохранить',
          text: 'Внимание! Есть расхождения между данными по суммам НДС в текущей схеме и актуальными данными. Подробная информация содержится в карточке детализации связи. При передаче данных в РАВП, суммы НДС в схеме будут актуализированы.',
          onConfirm: this._send,
          onCancel: this.props.onClose,
        }}
      />
    );
  }
}

export default DifferentSum;
